export class ChessPiece {
    posX;
    posY;
    player;
    main;

    availablePositions() {
        return [];
    }

    positionsUnderAttack() {
        return [];
    }

    clone(piece) {
        piece.posX = this.posX;
        piece.posY = this.posY;
        piece.player = this.player;
        piece.main = this.main;
    }
}