import {ChessHistory} from "./chessHistory";

export class Board {
    scaleX;
    scaleY;
    chessPieces;
    players;
    currentPlayer;
    history;

    fieldIsUnderAttack(x, y, player) {
        return !!this.chessPieces.find(piece => {
            if (piece.player === player) {
                return false;
            }
            const positions = piece.positionsUnderAttack(this);
            return positions.find(pos => pos[0] === x && pos[1] === y);
        })
    }

    getPiece(x, y) {
        return this.chessPieces.find(piece => {
            return piece.posX === x && piece.posY === y
        });
    }

    movePiece(piece, x, y) {
        if (piece) {
            const fromX = piece.posX;
            const fromY = piece.posY;
            const success = this.movePieceWithoutHistory(piece, x, y);
            if (success) {
                this.history.write(fromX, fromY, x, y);
            }
        }
    }

    movePieceWithoutHistory(piece, x, y) {
        if (piece) {
            const targetPiece = this.getPiece(x, y);
            if (targetPiece) {
                const index = this.chessPieces.indexOf(targetPiece);
                this.chessPieces.splice(index, 1);
            }
            piece.posX = x;
            piece.posY = y;

            let nextPlayer = this.players.indexOf(this.currentPlayer) + 1;
            if (nextPlayer >= this.players.length) {
                nextPlayer = 0;
            }
            this.currentPlayer = this.players[nextPlayer];
            return true;
        }
        return  false;
    }

    initHistory() {
        this.history = new ChessHistory();
        this.history.init(this);
    }

}